import headerNav from '@/components/header_nav.vue'
export default {
	data() {
		return {
			title: '反馈客服',
			up: '问题反馈',
			url_ri:'/pages/center/question',
			icon: '../../static/icon/left_whitch.png',
			page: 0,
			pages: 1,
			loadshow: true,
			sercontlist:[],
			current: 0,
			typess:1,
			tab: [{
				name: '处理中'
			}, {
				name: '已处理'
			}],
		}
	},
	methods: {
		tabs(index) {
			this.current = index
			let type = index + 1;
			this.typess = type;
			this.page = 0,
			this.pages = 1,
			this.loadshow = true,
			this.sercontlist = [],
			this.Loadthepage(type);
		},
		Loadthepage(e) {
			var self = this;
			if (!self.loadshow) {
				return
			}
			if (self.page < self.pages) {
				var page = self.page + 1;
				self.$service.auth(self, self.$service.api.service.workorderList, "GET", {
					page: page,
					limit: 15,
					type: e
				}, function(self, res) {
					console.log(res)
					self.pages = res.data.list.last_page;
					self.page = page;
					// self.activity = res.data.activity
					var list = res.data.list.data;
					list.forEach((v, i) => {
						self.sercontlist.push(v);
					});
					if (self.pages == self.page || self.sercontlist.length == 0) {
						console.log("false");
						self.loadshow = false
						uni.showToast({
							title: '数据已加载完毕',
							icon: 'none'
						});
					} else {
						self.loadshow = true
					}
				});
			} else {
				self.loadshow = false
				uni.showToast({
					title: '数据已加载完毕',
					icon: 'none'
				});
			}
		},
	},
	components: {
		headerNav
	},
	onReachBottom() {
		let type = this.typess
		this.Loadthepage(type);
	},
	onShow() {
		/* this.$service.auth(this , this.$service.api.service.workorderList , "GET" , {limit:15 , page:1 , type:1} , function(self , res){
			console.log(res.data.list.data);
		}) */
		this.Loadthepage(1);
	}
}
